acity 0.2s ease-out;\n  transition: opacity 0.2s ease-out;\n}\n.popover-appear-enter-from {\n  opacity: 0;\n}\n", "",{"version":3,"sources":["webpack://./node_modules/@abdulraof628/vue-guided-tour/src/components/vueGuidedTour.vue"],"names":[],"mappings":";AAkZA;EACE,kBAAkB;EAClB,MAAM;EACN,OAAO;EACP,yBAAyB;AAC3B;;AAEA;;;CAGC;AAED;EACE,gBAAgB;EAChB,gBAAgB;EAChB,mBAAmB;AACrB;AACA;EACE,iBAAiB;EACjB,aAAa;EACb,mBAAmB;AACrB;AACA;EACE,eAAe;EACf,SAAS;AACX;AAEA;EACE,oBAAa;EAAb,oBAAa;EAAb,aAAa;EACb,yBAAmB;MAAnB,sBAAmB;UAAnB,mBAAmB;AACrB;AACA;EACE,kBAAkB;EAClB,gBAAgB;EAChB,cAAc;AAChB;AACA;EACE,iBAAiB;AACnB;AACA;EACE,qBAAqB;EACrB,iBAAiB;EACjB,oBAAoB;EACpB,iBAAiB;EACjB,gBAAgB;EAChB,iBAAiB;EACjB,eAAe;EACf,qBAAqB;EACrB,kBAAkB;EAClB,8BAAsB;UAAtB,sBAAsB;EACtB,YAAY;EACZ,+DAAuD;EAAvD,uDAAuD;AACzD;AACA;EACE,yBAAyB;EACzB,WAAW;AACb;AACA;EACE,yBAAyB;AAC3B;AAEA;EACE,6BAA6B;EAC7B,cAAc;AAChB;AACA;EACE,cAAc;AAChB;AAEA;EACE,iBAAiB;AACnB;AACA;;;CAGC;AAED;EACE,kBAAkB;EAClB,MAAM;EACN,QAAQ;EACR,YAAY;EACZ,6BAA6B;EAC7B,eAAe;EACf,yCAAyC;EACzC,iBAAiB;EACjB,gBAAgB;EAChB,YAAY;EACZ,iBAAiB;EACjB,WAAW;EACX,UAAU;EACV,SAAS;EACT,cAAc;EACd,mCAA2B;EAA3B,2BAA2B;AAC7B;AACA;EACE,cAAc;AAChB;AAEA;EACE,yCAAiC;EAAjC,iCAAiC;AACnC;AACA;EACE,UAAU;AACZ","sourcesContent":["<template>\n  <teleport to=\"body\">\n    <div\n      ref=\"vgtRef\"\n      class=\"vue-guided-tour\"\n      v-bind=\"$attrs\"\n    >\n      <vgt-overlay\n        v-if=\"useOverlay\"\n        ref=\"vgtOverlay\"\n        :rect=\"currentStepRect\"\n        :allow-overlay-close=\"allowOverlayClose\"\n        :allow-esc-close=\"allowEscClose\"\n        :allow-interaction=\"allowInteraction\"\n        v-bind=\"{ ...currentStep.overlay }\"\n      />\n      <template\n        v-if=\"\n          showPopover &&\n            (currentStep.title || currentStep.content || $slots.content)\n        \"\n      >\n        <transition\n          name=\"popover-appear\"\n          appear\n        >\n          <vgt-popover\n            :rect=\"currentStepRect\"\n            :arrow=\"arrow\"\n            :offset=\"offset\"\n            :position=\"position\"\n            :placement=\"placement\"\n            :auto-adjust=\"autoAdjust\"\n            v-bind=\"{ ...currentStep.popover }\"\n          >\n            <div class=\"vgt__body\">\n              <slot\n                name=\"content\"\n                v-bind=\"{ stepIndex: currentStepIndex }\"\n              >\n                <h3\n                  v-if=\"currentStep.title\"\n                  :id=\"currentStep.popover['aria-labelledby']\"\n                  class=\"vgt__title\"\n                >\n                  {{ currentStep.title }}\n                </h3>\n                <div\n                  v-if=\"currentStep.content\"\n                  :id=\"currentStep.popover['aria-describedby']\"\n                  class=\"vgt__content\"\n                >\n                  {{ currentStep.content }}\n                </div>\n              </slot>\n            </div>\n            <div class=\"vgt__footer\">\n              <div\n                v-if=\"pagination\"\n                class=\"vgt__pages\"\n              >\n                {{ currentStepIndex + 1 }} / {{ steps.length }}\n              </div>\n              <slot\n                name=\"nav\"\n                v-bind=\"{ isFirstStep, isLastStep }\"\n              >\n                <div class=\"vgt__nav\">\n                  <button\n                    v-if=\"!isFirstStep\"\n                    class=\"vgt__btn vgt__btn--secondary vgt__prev-btn\"\n                    @click=\"prev\"\n                  >\n                    Prev\n                  </button>\n                  <button\n                    v-if=\"isLastStep\"\n                    class=\"vgt__btn vgt__btn--primary vgt__end-btn\"\n                    @click=\"end\"\n                  >\n                    Got it.\n                  </button>\n                  <button\n                    v-else\n                    class=\"vgt__btn vgt__btn--primary vgt__next-btn\"\n                    @click=\"next\"\n                  >\n                    Next\n                  </button>\n                </div>\n              </slot>\n            </div>\n          </vgt-popover>\n        </transition>\n      </template>\n    </div>\n  </teleport>\n</template>\n\n<script>\nimport VueGuidedOverlay from \"./vueGuidedOverlay.vue\";\nimport VueGuidedPopover from \"./vueGuidedPopover.vue\";\nimport { ref, computed, onMounted, inject, nextTick, toRefs } from \"vue\";\nimport { isInView, getBoundingWithPadding } from \"../use/utils\";\nimport useRect from \"../use/useRect\";\nimport useEvent from \"../use/useEvent\";\nimport useFocusTrap from \"../use/useFocusTrap\";\nimport { vgtProps } from \"../propsValidation\";\n\nexport default {\n  name: \"VueGuidedTour\",\n  components: {\n    \"vgt-overlay\": VueGuidedOverlay,\n    \"vgt-popover\": VueGuidedPopover,\n  },\n  inheritAttrs: false,\n  props: {\n    ...vgtProps,\n  },\n  emits: [\"update:stepIndex\", \"afterStart\", \"afterEnd\", \"afterMove\"],\n  setup(props, { emit }) {\n    const { stepIndex, steps, allowKeyboardEvent, useOverlay } = toRefs(props);\n    const vgtRef = ref(null);\n    const vgtOverlay = ref(null);\n\n    const $vgt = inject(\"$vgt\", {});\n    const uid = Math.random().toString(36).substring(2);\n\n    const showPopover = ref(false);\n\n    const currentStepIndex = ref(-1);\n    currentStepIndex.value = stepIndex.value;\n\n    const currentStepEl = ref(null);\n    const prevEl = ref(null);\n\n    const active = ref(false);\n    const moving = ref(false);\n\n    const { enableTrap } = useFocusTrap(vgtRef);\n    let lastFocused;\n\n    const { getHighlightEl, addHighlight, removeHighlight } = useHightlight();\n\n    const { rect } = useRect(currentStepEl);\n    const currentStepRect = computed(() => {\n      const padding = currentStep.value.padding || props.padding;\n      return getBoundingWithPadding(rect, padding);\n    });\n\n    const currentStep = computed(() => {\n      if (currentStepIndex.value < 0) return {};\n      const stepObj = steps.value[currentStepIndex.value];\n      if (!stepObj) return {};\n      return {\n        ...stepObj,\n        // popover options\n        popover: {\n          ...stepObj.popover,\n          role: \"dialog\",\n          id: stepObj.popover?.id || `popover-${uid}`,\n          \"aria-labelledby\": stepObj.title\n            ? `${stepObj.popover?.id || uid}-title`\n            : null,\n          \"aria-describedby\": stepObj.content\n            ? `${stepObj.popover?.id || uid}-desc`\n            : null,\n        },\n        // overlay options\n        overlay: {\n          ...stepObj.overlay,\n        },\n      };\n    });\n    const isFirstStep = computed(() => {\n      return currentStepIndex.value === 0;\n    });\n    const isLastStep = computed(() => {\n      return currentStepIndex.value === steps.value.length - 1;\n    });\n\n    onMounted(() => {\n      $vgt.start = onStart;\n      $vgt.next = onNext;\n      $vgt.prev = onPrev;\n      $vgt.end = onEnd;\n      $vgt.move = onMove;\n    });\n\n    const onStart = (index = 0) => {\n      if (active.value) return;\n      handleStepIndexChange(index);\n    };\n\n    const onNext = () => {\n      if (!active.value || moving.value) return;\n      const index = currentStepIndex.value + 1;\n      if (index > steps.value.length - 1) return;\n      handleStepIndexChange(index);\n    };\n\n    const onPrev = () => {\n      if (!active.value || moving.value) return;\n      const index = currentStepIndex.value - 1;\n      if (index < 0) return;\n      handleStepIndexChange(index);\n    };\n\n    const onEnd = () => {\n      if (!active.value || moving.value) return;\n      const index = -1;\n      handleStepIndexChange(index);\n    };\n\n    const onMove = (index = 0) => {\n      if (index === currentStepIndex.value || !active.value || moving.value)\n        return;\n      handleStepIndexChange(index);\n    };\n\n    const handleStepIndexChange = (index) => {\n      if (index < -1 || index > steps.value.length - 1) return;\n      if (index === currentStepIndex.value) return;\n\n      const el = getHighlightEl(index, steps.value);\n      if (!el && index !== -1) return;\n\n      showPopover.value = false;\n      removeHighlight();\n\n      updateCurrentStep(index, el);\n\n      if (index === -1) {\n        handleEndTour();\n        return;\n      }\n      handleMoveTour();\n    };\n\n    const handleMoveTour = () => {\n      const el = currentStepEl.value;\n      const move = !!prevEl.value;\n\n      active.value = true;\n      moving.value = true;\n\n      if (useOverlay.value) {\n        const startTour = () => {\n          lastFocused = document.activeElement;\n          vgtOverlay.value.overlayStart(done);\n        };\n\n        const moveTour = () => {\n          const padding = currentStep.value.padding || props.padding;\n          const newRect = getBoundingWithPadding(\n            currentStepEl.value.getBoundingClientRect(),\n            padding\n          );\n          vgtOverlay.value.overlayMoveTo(newRect, done);\n        };\n\n        move ? moveTour() : startTour();\n      } else {\n        nextTick(() => {\n          done();\n        });\n      }\n\n      function done() {\n        const inView = isInView(el.getBoundingClientRect());\n        if (!inView) {\n          el.scrollIntoView({ block: \"center\" });\n        }\n        showPopover.value = true;\n        addHighlight(currentStepEl.value);\n        moving.value = false;\n        nextTick(() => {\n          enableTrap();\n        });\n        emit(!move ? \"afterStart\" : \"afterMove\");\n      }\n    };\n\n    const handleEndTour = () => {\n      showPopover.value = false;\n      removeHighlight();\n      if (useOverlay.value) {\n        endTour();\n      } else {\n        done();\n      }\n\n      function endTour() {\n        vgtOverlay.value.overlayClose(done);\n      }\n\n      function done() {\n        active.value = false;\n        moving.value = false;\n        lastFocused.focus({\n          preventScroll: true,\n        });\n        emit(\"afterEnd\", true);\n      }\n    };\n\n    const updateCurrentStep = (index = -1, el) => {\n      currentStepIndex.value = index;\n      emit(\"update:stepIndex\", currentStepIndex.value);\n\n      if (index === -1) {\n        prevEl.value = null;\n        currentStepEl.value = null;\n      } else {\n        prevEl.value = currentStepEl.value;\n        currentStepEl.value = el;\n      }\n    };\n\n    const onKeyUp = (event) => {\n      if (!active.value || !allowKeyboardEvent.value) return;\n      switch (event.key) {\n        case \"ArrowLeft\":\n          onPrev();\n          break;\n        case \"ArrowRight\":\n          onNext();\n          break;\n      }\n    };\n\n    const onOverlayClick = () => {\n      onEnd();\n    };\n\n    const onCloseClick = () => {\n      onEnd();\n    };\n\n    useEvent(window, \"keyup\", onKeyUp);\n\n    return {\n      vgtRef,\n      vgtOverlay,\n      active,\n      showPopover,\n      currentStepIndex,\n      currentStepRect,\n      currentStep,\n      isFirstStep,\n      isLastStep,\n      start: onStart,\n      next: onNext,\n      prev: onPrev,\n      end: onEnd,\n      move: onMove,\n      onOverlayClick,\n      onCloseClick,\n    };\n  },\n};\n\nfunction useHightlight() {\n  const highlightClass = \"vgt__target--highlighted\";\n\n  const getHighlightEl = (index, steps) => {\n    if (typeof index !== \"number\" || index < 0 || index > steps.length - 1)\n      return;\n    const targetValue = steps[index].target;\n    const el = document.querySelector(`${targetValue}`);\n    if (!targetValue) {\n      console.warn(`[vue-guided-tour] : Target is required in step ${index}`);\n      return undefined;\n    } else if (!el) {\n      console.warn(\n        `[vue-guided-tour] : Target to highlight \"${targetValue}\" not found`\n      );\n      return undefined;\n    }\n    return el;\n  };\n\n  const addHighlight = (el) => {\n    el.classList.add(highlightClass);\n  };\n\n  const removeHighlight = () => {\n    const allTargets = document.querySelectorAll(`.${highlightClass}`);\n    allTargets.forEach((target) => {\n      target.classList.remove(highlightClass);\n    });\n  };\n\n  return {\n    getHighlightEl,\n    addHighlight,\n    removeHighlight,\n  };\n}\n</script>\n\n<style>\n.vue-guided-tour {\n  position: absolute;\n  top: 0;\n  left: 0;\n  z-index: 99999 !important;\n}\n\n/*\n.vgt__target--highlighted {\n}\n*/\n\n.vgt__body {\n  line-height: 1.5;\n  margin-top: 30px;\n  margin-bottom: 20px;\n}\n.vgt__title {\n  font-size: 1.4rem;\n  margin-top: 0;\n  margin-bottom: 10px;\n}\n.vgt__content {\n  font-size: 1rem;\n  margin: 0;\n}\n\n.vgt__footer {\n  display: flex;\n  align-items: center;\n}\n.vgt__pages {\n  font-size: 0.76rem;\n  font-weight: 500;\n  color: #969faf;\n}\n.vgt__nav {\n  margin-left: auto;\n}\n.vgt__btn {\n  display: inline-block;\n  padding: 5px 16px;\n  font-family: inherit;\n  font-size: 0.8rem;\n  font-weight: 500;\n  line-height: 20px;\n  cursor: pointer;\n  text-decoration: none;\n  border-radius: 4px;\n  box-sizing: border-box;\n  border: none;\n  transition: background-color 0.1s ease, color 0.1s ease;\n}\n.vgt__btn--primary {\n  background-color: #3eaf7c;\n  color: #fff;\n}\n.vgt__btn--primary:hover {\n  background-color: #4abf8a;\n}\n\n.vgt__btn--secondary {\n  background-color: transparent;\n  color: #969faf;\n}\n.vgt__btn--secondary:hover {\n  color: #5b6474;\n}\n\n.vgt__prev-btn {\n  margin-right: 7px;\n}\n/*\n.vgt__next-btn {}\n.vgt__end-btn {}\n*/\n\n.vgt__close-btn {\n  position: absolute;\n  top: 0;\n  right: 0;\n  border: none;\n  background-color: transparent;\n  cursor: pointer;\n  font-family: Arial, Helvetica, sans-serif;\n  font-size: 0.8rem;\n  font-weight: 700;\n  height: 36px;\n  line-height: 36px;\n  width: 36px;\n  padding: 0;\n  margin: 0;\n  color: #969faf;\n  transition: color 0.1s ease;\n}\n.vgt__close-btn:hover {\n  color: #5b6474;\n}\n\n.popover-appear-enter-active {\n  transition: opacity 0.2s ease-out;\n}\n.popover-appear-enter-from {\n  opacity: 0;\n}\n</style>"],"sourceRoot":""}]);
